const styles = theme => ({
  portalDashboardPageWrapper: {
    padding: 16,
    minHeight: '100%',
    boxSizing: 'border-box'
  },
  portalWidget: {
    flex: '1 1 100%',
    display: 'flex',
    flexDirection: 'column'
  },
  portalWidgetContent: {
    flex: '1 1 100%',
    minHeight: '100px'
  }
});

export default styles;



